.program-item {
  width: 280px;
  background-color: $second-color;
  padding: 26px 20px 18px 20px;
}

.program-item:not(:first-child) {
  margin: 27px 0 0 0;
}

.program-item__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
}

.program-item__title {
  margin: 0;
  font-size: 24px;
  line-height: 37px;
  font-weight: 400;
  font-family: $Oswald;
  text-transform: uppercase;
  padding-bottom: 33px;
  padding-left: 69px;
}

.program-item__wrapper::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: $first-color;
}

.program-item__illustration-svg--thin {
  position: absolute;
  top: -4px;
  left: 7px;
}

.program-item__illustration-svg--fat {
  position: absolute;
  top: 10px;
  left: -11px;
}

.program-item__description {
  @include index-text;
  margin: 0;
  padding-bottom: 22px;
  border-bottom: 1px solid $border-color;
}

.program-item__button {
  text-decoration: none;
  padding-top: 17px;
  display: flex;
  align-items: center;

  &:active {
    opacity: 30%;
  }
}

.program-item__subtitle {
  display: block;
  color: $color-text-second;
  font-size: 16px;
  line-height: 16px;
  align-self: flex-start;
  margin: 0;
  text-transform: uppercase;
  padding-right: 17px;
}

.program-item__polygon--big {
  display: none;
}

.program-item__button:hover .program-item__polygon {
  display: none;
}

.program-item__button:hover .program-item__polygon--big {
  display: block;
}

// tablet

@media (min-width: $tablet-width) {
  .program-item {
    width: 634px;
    padding: 77px 62px 53px 52px;
  }

  .program-item:not(:first-child) {
    margin-top: 30px;
  }

  .program-item__wrapper {
    align-items: flex-start;

    &::before {
      top: 16px;
      right: 0;
      left: initial;
      width: 200px;
      height: 200px;
    }
  }

  .program-item__title {
    font-size: 36px;
    line-height: 36px;
    padding-bottom: 27px;
    padding-left: 0;
  }

  .program-item__illustration-svg--thin {
    top: 5px;
    left: 425px;
    width: 140px;
    height: 194px;
  }

  .program-item__illustration-svg--fat {
    top: 63px;
    left: 360px;
    width: 268px;
    height: 112px;
  }

  .program-item__description {
    @include index-text-tablet;
    width: 340px;
    border-bottom: none;
    padding-bottom: 52px;
  }

  .program-item__button {
    padding-top: 0;
  }

  .program-item__subtitle {
    font-size: 20px;
    line-height: 29.64px;
    padding-right: 25px;
  }
}

// desktop

@media (min-width: $desktop-width) {
  .program-item {
    width: 459px;
    padding: 41px 51px 55px 52px;
  }

  .program-item:not(:first-child) {
    margin: 0;
  }

  .program-item:nth-child(odd) {
    margin-right: 87px;
  }

  .program-item__wrapper::before {
    top: 0;
    left: 0;
    width: 100px;
    height: 100px;
  }

  .program-item__illustration-svg--thin {
    top: -3px;
    left: 20px;
    width: 70px;
    height: 97px;
  }

  .program-item__illustration-svg--fat {
    top: 24px;
    left: -18px;
    width: 134px;
    height: 56px;
  }

  .program-item__title {
    padding: 33px 10px 73px 162px;
  }

  .program-item__description {
    width: 467px;
    padding-bottom: 27px;
  }

  .program-item__subtitle {
    padding-right: 28px;
  }
}
