.base-group {
  border: none;
  padding: 0;
  margin: 0;
}

.base-group__wrapper {
  border-bottom: 1px solid $border-color-catalog;
}

.base-group__container {
  width: 280px;
  margin: 0 auto;
}

.base-group__list--input {
  list-style-type: none;
  margin: 0;
  padding: 21px 0 24px 0;
}

.base-group__list--radio {
  margin: 0;
  padding: 22px 0 17px 0;
}

.base-group__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 11px;
}

.base-group__item:last-child {
  margin-bottom: 0;
}

.base-group__label {
  @include data-form-text;
  color: $form-text-first;
  display: flex;
  cursor: pointer;
  white-space: nowrap;
}

.base-group__description {
  @include data-form-text;
  color: $form-text-second;
  padding: 7px 0 7px 15px;
}

.base-group__input {
  @include data-form-text;
  cursor: pointer;
  color: $form-text-first;
  width: 100%;
  padding: 11px 13px 10px 16px;
  margin-left: 12px;
  border: 1px solid $input-color;

  &:hover {
    outline: 1px solid $border-color-catalog;
  }

  &:focus {
    outline: 2px solid $form-text-first;
  }

  &:invalid {
    outline: 2px solid $border-color-invalid;
  }
}

.base-group__input:disabled ~ .base-group__description {
  opacity: 30%;
}

// tablet

@media (min-width: $tablet-width) {
  .base-group__wrapper {
    border-bottom: none;
    margin-bottom: 50px;
  }

  .base-group__container {
    width: 630px;
    margin: 0 auto;
  }

  .base-group__list {
    border: none;
  }

  .base-group__item {
    margin-bottom: 16px;
  }

  .base-group__item--radio {
    margin-bottom: 13px;
  }

  .base-group__list--radio {
    padding: 23px 27px 24px 29px;
    border: 1px solid $input-color;
  }

  .base-group__list:nth-child(3) {
    border: 2px solid #e7e7e7;
  }

  .base-group__label {
    @include data-form-text-tablet;
    color: $form-text-first;
  }

  .base-group__description {
    @include data-form-text-tablet;
    color: $form-text-second;
    padding: 5px 0 5px 19px;
  }

  .base-group__input {
    @include data-form-text-tablet;
    color: $form-text-first;
    padding: 11px 18px 12px 18px;
    margin-left: 19px;
  }
}

// desktop

@media (min-width: $desktop-width) {
  .base-group {
    display: flex;
    width: 1220px;
    margin: 0 auto;
  }

  .base-group__container {
    width: 569px;
  }

  .base-group__list {
    width: 568px;
    padding: 0;
  }

  .base-group__list:nth-child(2) {
    margin-right: 80px;
  }

  .base-group__list--radio {
    width: 512px;
    padding: 25px 28px 25px 28px;
    margin-left: 80px;
  }

  .base-group__item {
    margin-bottom: 16px;
  }

  .base-group__list--radio .base-group__item {
    margin-bottom: 13px;
  }

  .base-group__list--radio .base-group__item:last-child {
    margin-bottom: 0;
  }

  .base-group__input {
    padding: 11px 18px 12px 18px;
  }
}
